.project__container{
    width: 40%; 
    
}

h3{
    margin-top: 0.5rem;
    text-align: center;
}

.project__item{
    background: #1b0000;
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    text-align: center;

}

.project__item:hover{
    background: transparent;
    border-color: #1b0000;
}

.project__item-image{
    border-radius: 1.5rem;
    overflow: hidden;
    width: 80%;
    display: flex;
    position: relative;
    left: 4rem;
}

.project__item h3{
margin: 1.2rem 0 2rem;
text-align: center;
}

.project__item-cta{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
    justify-content: center;
    

}

/* TABLET */
@media screen and (max-width: 1024px) {
    .project__container {
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
}

/* PHONES */
@media screen and (max-width: 600px) {
    .project__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }
    
}